.images-form {
  position: absolute;
  top: 143px;
  width: calc(100% - 36px - 36px);
  max-width: 1280px;
  left: 50%;
  transform: translateX(-50%);
  background-color: $white;
  z-index: -100;
  border-radius: 10px;
  overflow: hidden;
  max-height: 90vh;
  transition: all .3s ease-in-out;
  &__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    .cursor {
      cursor: pointer;
    }
    img {
      height: 100%;
      object-fit: contain;
    }
  }
  &__top {
    padding: 50px 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__nav-btns {
    position: absolute;
    bottom: 67px;
    left: 0;
    z-index: -1;
    display: flex;
    opacity: 0;
    width: 100%;
    justify-content: space-between;
  }
  .swiper-modal {
    width: 100%;
    height: 418px;
  }
  .swiper-modal-bottom {
    padding: 20px 51px;
    height: 166px;
    background-color: $grey-light;
  }
}

@media (max-width: 1280px) {
  .images-form {
    &__nav-btns {
      opacity: 1;
    }
    .swiper-modal {
      height: 322px;
    }
  }
}
@media (max-width: 900px) {
  .images-form {
    .swiper-modal {
      height: 228px;
    }
  }
}
@media (max-width: 768px) {
  .images-form {
    &__top {
      padding: 50px 20px;
    }

  }
}
@media (max-width: 576px) {
  .images-form {
    &__nav-btns {
      bottom: 43px;
    }
    .swiper-modal {
      height: 104px;
    }
    .swiper-modal-bottom {
      height: 118px;
    }
    width: calc(100% - 24px);
  }
}
