.item {
  padding-top: 29px;
  padding-bottom: 41px;
  &__content {
    @extend %grid;
    grid-row-gap: 5px;
    position: relative;
  }
  &__block {
    grid-column: span 6;
  }
  &__image {
    grid-column: span 6;
    margin-bottom: 11px;
    cursor: pointer;
  }
  &__image2 {
    grid-column: span 6;
    cursor: pointer;
    margin-bottom: 23px;
  }
  &__images {
    margin-bottom: 28px;
    cursor: pointer;
  }
  &__descr-title {
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
    color: $black;
    margin-bottom: 8px;
  }
  &__descr {
    font-weight: 400;
    font-size: 14px;
    line-height: 180%;
    color: $black;
  }
  &__descr1 {
    margin-bottom: 8px;
  }
  &__title {
    margin-bottom: 5px;
    font-size: 24px;
    line-height: 130%;
  }
  &__subtitle {
    margin-bottom: 20px;
  }
  &__btn {
    padding: 19px 35px;
    font-weight: 600;
    margin-bottom: 12px;
    display: block;
  }
  &__btn-add {
    font-size: 16px;
    line-height: 130%;
    color: $primary;
    margin-left: 30px;
  }
  &__price {
    display: block;
    font-size: 36px;
    line-height: 130%;
    color: $black;
    margin-bottom: 21px;
  }
  &__image-bottom {
    grid-column: span 6;
    align-items: end;
    position: absolute;
    bottom: 0;
    left: 0;
  }
  &__top {
    grid-row: span 2;
    padding-top: 17px;
    grid-column: span 6;
    .stars {
      position: unset;
    }
  }
  &__summary-item {
    font-size: 14px;
    line-height: 179%;
    color: $black;
  }
  &__summary-left {
    display: flex;
    span {
      font-size: 14px;
      line-height: 179%;
      color: $grey;
    }
    .dots {
      flex-grow: 1;
      border-bottom: 2px dotted $grey;
      transform: translate(4px, -7px) scaleX(1.1);
      transform-origin: left;
    }
  }
  &__summary {
    grid-row: span 2;
    grid-column: span 6;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 32px;
    .summary-left,
    .summary-right {
      grid-column: span 3;
    }
  }
}

@media (max-width: 1140px) {
  .breadcrumbs--item {
    margin-top: -14px;
  }
  .item {
    padding-bottom: 69px;
    &__content {
      grid-template-columns: repeat(9, 1fr);
    }
    &__image,
    &__image2,
    &__block,
    &__image-bottom {
      grid-column: span 5;
    }
    &__image-bottom {
      max-width: 514px;
    }
    &__top {
      grid-column: span 4;
    }
    &__summary {
      grid-column: span 4;
      grid-template-columns: repeat(4, 1fr);
      gap: 0;
      .summary-left,
      .summary-right {
        grid-column: span 2;
      }
      .summary-left {
        max-width: 183px;
        overflow: hidden;
      }
      .summary-right {
        margin-left: -13px;
      }
      .dots {
        transform: translate(1px, -7px) scaleX(1);
      }
    }
  }
}
@media (max-width: 1023px) {
  .item {
    &__summary {
      grid-column: span 9;
      order: 1;
      .summary-left {
        max-width: unset;
      }
    }
    &__block {
      grid-column: span 9;
    }
    &__image-bottom {
      position: unset;
    }
  }
}
@media (max-width: 900px) {
  .item {
    padding-bottom: 31px;
    &__content {
      grid-template-columns: repeat(6, 1fr);
    }
    &__image {
      grid-column: span 6;
      order:1;
      margin-bottom: 27px;
    }
    &__image2 {
      grid-column: span 2;
      order: 2;
      margin-bottom: 18px;
    }
    &__top {
      padding-top: 53px;
      grid-column: span 4;
      order: 3;
    }
    &__block {
      order: 4;
      grid-column: span 5;
      margin-bottom: 26px;
    }
    &__image-bottom {
      order: 5;
      margin-bottom: 27px;
    }
    &__summary {
      order: 6;
      grid-column: span 6;
      grid-template-columns: repeat(6, 1fr);
      .summary-left {
        grid-column: span 4;
      }
      .summary-right {
        margin-left: -8px;
      }
    }
  }
}
@media (max-width: 765px) {
  .item {
    &__summary {
      .summary-left {
        grid-column: span 3;
        max-width: 405px;
      }
      .summary-right {
        grid-column: span 3;
      }
    }
  }
}
@media (max-width: 576px) {
  .item {
    padding-bottom: 51px;
    padding-top: 0;
    &__image,
    &__image2,
    &__top,
    &__block,
    &__summary {
      grid-column: span 6;
    }
    &__image {
      margin-bottom: 11px;
    }
    &__image2 {
      margin-bottom: 0;
    }
    &__top {
      padding-top: 7px;
      margin-bottom: 26px;
    }
    &__title {
      font-size: 16px;
      line-height: 130%;
      margin-bottom: 8px;
    }
    &__price {
      font-size: 24px;
      line-height: 130%;
      margin-bottom: 15px;
    }
    &__subtitle {
      margin-bottom: 17px;
    }
    &__btn {
      width: 100%;
    }
    &__btn-add {
      margin: 0 auto;
      display: block;
    }
    &__image-bottom {
      max-width: 296px;
    }
    .dots {
      display: none;
    }
    .stars {
      margin-bottom: 2px;
      &__value {
        font-size: 16px;
        line-height: 130%;
      }
      img {
        width: 16px;
        height: 15px;
      }
    }
    &__summary {
      display: initial;
      position: relative;
    }
    .summary-right {
      margin-left: 0;
      margin-top: 11px;
    }
    .summary-left {
      position: absolute;
    }
    &__summary-left {
      margin-bottom: 27px;
      span {
        font-size: 10px;
        line-height: 14px;
      }
    }
    &__summary-item {
      margin-bottom: 16px;
    }
  }
}
