.breadcrumbs {
  margin-top: -14px;
  &--item {
    margin-top: -16px;
  }
  &__list {
    display: flex;
  }
  &__link {
    position: relative;
    margin-right: 15px;
    &::after {
      content: "/";
      position: absolute;
      top: 2px;
      right: -11px;
    }
    &--disabled {
      color: $primary-shade;
      &::after {
        content: "";
      }
    }
  }
}
