.collection {
  background-image: url('../img/oxford.webp');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-top: 156px;
  padding-bottom: 149px;
  &__container {
    @extend %grid;
  }
  &__content {
    grid-column: span 5;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-column-gap: 32px;
    grid-row-gap: 49px;
  }
  &__top {
    grid-column: span 5;
  }
  &__title {
    font-family: 'Playfair Display';
    font-weight: 900;
    font-size: 60px;
    line-height: 116%;
    color: $gold;
  }
  &__descr {
    font-size: 48px;
    line-height: 120%;
    color: $white;
  }
  &__btn {
    grid-column: span 2;
  }
}
@media (max-width: 1140px) {
  .collection__content {
    grid-column: span 7;
  }
}
@media (max-width: 1024px) {
  .collection {
    background-image: url('../img/oxford-1024.webp');
    &__content {
      grid-column: span 5;
    }
  }
}
@media (max-width: 900px) {
  .collection__content {
    grid-column: span 7;
  }
}
@media (max-width: 768px) {
  .collection {
    background-image: url('../img/oxford-768.webp');
    &__container {
      padding: 0 36px;
    }
    &__content {
      grid-column: span 5;
    }
    &__btn {
      margin-right: 24px;
    }
  }
}
@media (max-width: 576px) {
  .collection {
    padding-top: 32px;
    padding-bottom: 0;
    background-image: url('../img/oxford-320.webp');
    min-height: 210px;
    position: relative;
    margin-bottom: 91px;
    &__title {
      font-size: 24px;
      line-height: 120%;
    }
    &__descr {
      font-size: 16px;
      line-height: 120%;
    }
    &__container {
      padding: 0 12px;
    }
    &__content {
      grid-column: span 4;
    }
    &__btn {
      width: calc(100% - 24px);
      position: absolute;
      bottom: -92px;
    }
  }
}
