// colors
$primary:#A65CF0;
$primary-light: #C998FB;
$primary-shade:#7033AC;
$pink-light: #F8EFFF;
$pink-violet:  #EBD1FF;
$secondary: #FF862F;
$secondary-light: #FFBD59;
$secondary-shade: #BB370E;
$red-input:#FF6972;
$green-input: #B8EC64;
$gold: #D5B986;
$white: #fff;
$grey-light: #F7F7F7;
$md-grey: #EAEAEA;
$grey: #999;
$grey-shade:#666;
$black:#333;
$lime-color: #DAFFD1;
$bone: #FFF5D1;
$background-20: rgba(#000, 0.2);

// 12-column grid
%grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 32px;
}

@media (max-width: 1024px) {
  %grid {
    grid-template-columns: repeat(9, 1fr);
  }
}
@media (max-width: 768px) {
  %grid {
    grid-template-columns: repeat(6, 1fr);
  }
}
@media (max-width: 576px) {
  %grid {
    gap: 16px;
  }
}
