.card {
  min-height: 420px;
  position: relative;
  grid-column: span 3;
  background-color: $grey-light;
  border-radius: 10px;
  &__top {
    position: relative;
    width: 100%;
    height: 191px;
  }
  &__bottom {
    width: 100%;
    padding: 30px;
    padding-top: 16px;
    min-height: 229px;
    height: calc(100% - 191px);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: start;
  }
  &__image {
    position: absolute;
    z-index: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    max-width: 90%;
  }
  .price {
    margin-bottom: 21px;
  }
  .price.stars__value {
    margin-bottom: 0;
  }
  .card-title {
    margin-bottom: 24px;
  }
}
.card-title-circle {
  display: none;
}
.buy-link {
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 0;
  }
}

@media (max-width: 576px) {
  .card{
    &__top {
      height: 90px;
    }
    &__bottom {
      height: calc(100% - 90px);
      min-height: 188px;
      padding: 16px;
    }
    .card-title {
      margin-bottom: 16px;
    }
    .price {
      margin-bottom: 12px;
    }
    .btn-stroke {
      padding: 10px 23px;
    }
  }
}

