.sign {
  padding-top: 35px;
  padding-bottom: 68px;
  &__container {
    @extend %grid;
  }
  &__image {
    grid-column: span 6;
    background-image: url('../img/back.webp');
    background-repeat: no-repeat;
    background-position: 95% 48%;
    background-size: contain;
  }
  &__content {
    grid-column: span 6;
  }
  &__title {
    margin-bottom: 5px;
  }
  &__text-container {
    display: inline;
  }
  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: $grey-shade;
  }

  &__modal {
    min-height: 421px;
    .form-answer {
      transform: none;
      opacity: 1;
    }
  }
  .tooltip {
    cursor: pointer;
  }
  .tippy-box {
    font-size: 9px;
    line-height: 14px;
    padding: 5px;
    border-radius: 0;
    text-align: center;
  }
  .tippy-content {
    padding: 0;
  }
}
.sign-form {
    position: relative;
    margin-top: 14px;
  &__input {
    margin-bottom: 30px;
    width: 100%;
    border-radius: 10px;
    background-color: $grey-light;
    padding: 7px 18px;
    font-weight: 400;
    font-size: 16px;
    outline: none;
    line-height: 40px;
    color: $black;
    font-family: 'Open sans', sans-serif;
    transition: background-color .3s ease-in-out;
    &::placeholder {
      color: $grey;
    }
    &:focus {
      outline: 2px solid $primary;
      background-color: $pink-light;
    }
    &:hover {
      background-color: $md-grey;
    }
  }
  &__input.is-invalid {
    outline: 2px solid $red-input;
  }
  &__input.just-validate-success-field {
    outline: 2px solid $green-input;
  }
  &__checkbox-text {
    font-size: 14px;
    line-height: 19px;
    color: $black;
    &::before {
      width: 14px;
      height: 14px;
    }
    &::after {
      width: 12px;
      height: 14px;
      background-size: 10px 8px;
    }
  }
  &__btn {
    padding: 19px 35px;
    margin-bottom: 11px;
    font-weight: 600;
  }
  &__link {
    color: $primary;
    text-decoration: underline;
    outline: none;
    &:focus {
      color: $primary-shade;
    }
  }
}
@media (max-width: 1024px) {
  .sign {
    &__image {
      grid-column: span 5;
      background-image: url('../img/back-1024.webp');
      background-position: 91% 49%;
    }
    &__content {
      grid-column: span 4;
    }
    .sign-form{
      margin-top: 10px;
    }
    .sign-form__btn {
      margin-bottom: 11px;
    }
  }
}
@media (max-width: 768px) {
  .sign {
    &__image {
      grid-column: span 2;
      background-image: url('../img/back-768.webp');
      background-position: 87.6% 49%;
    }
  }
}
@media (max-width: 576px) {
  .sign {
    padding-top: 59px;
    &__title {
      margin-bottom: 4px;
    }
    &__image{
      display: none;
    }
    .sign-form {
      margin-top: 8px;
    }
    &__content {
      grid-column: span 6;
    }
    .sign-form__btn {
      margin-bottom: 9px;
    }
    .sign-form__checkbox-text {
      font-size: 12px;
      line-height: 16px;
      padding-left: 20px;
    }
  }
}
