@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-family: "#{$font-family}";
    src: url('../fonts/#{$url}.woff2') format("woff2");
    font-weight: #{$weight};
    font-display: swap;
    font-style: $style;
  }
}
@include font-face("Open Sans", "opensans-regular", 400, normal);
@include font-face("Open Sans", "opensans-bold", 700, normal);
@include font-face("Open Sans", "opensans-semibold", 600, normal);
@include font-face("Playfair Display", "playfairdisplay-black", 900, normal);


