.rate {
  padding-top: 35px;
  padding-bottom: 70px;
  &__title {
    margin-bottom: 26px;
  }
  &__content {
    @extend %grid;
  }
  &__btn {
    grid-column: span 3;
    position: relative;
    left: 166%;
  }
}
.hidden {
  grid-column: span 12;
  @extend %grid;
  transform-origin: top;
  transform: scaleY(0);
  transition: all .3s ease-in-out;
  position: absolute;
  &--visible {
    transform: scaleY(1);
    position: unset;
  }
}
.stars {
  position: absolute;
  top: 30px;
  left: 30px;
  display: flex;
  align-items: center;
  &__value {
    color: $secondary;
  }
  img {
    margin-right: 6px;
  }
}
@media (max-width: 1024px) {
  .rate__card:nth-child(7),
  .rate__card:nth-child(8) {
    display: none;
  }
  .rate__btn {
    left: unset;
    grid-column-start: 4;
    grid-column-end: 7;
  }
}
@media (max-width: 768px) {
  .rate__btn {
    left: 55px;
    grid-column-start: 2;
    grid-column-end: 5;
  }
}
@media (max-width: 576px) {
  .rate {
    padding-top: 59px;
    &__btn {
      left: 0;
      grid-column: span 6;
      margin-top: 16px;
    }
    &__card {
      min-height: 278px;
      .btn-stroke {
        padding: 10px 23px;
      }

    }
  }
  .stars {
    top: 16px;
    left: 16px;
    &__value {
      font-size: 12px;
    }
    img {
      width: 13px;
      height: 12px;
    }
  }
}
