.modal {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba($black, .3);
  opacity: 0;
  z-index: -100;
  pointer-events: none;
  transition: all .3s ease-in-out;
  &--active {
    opacity: 1;
    z-index: 400;
    pointer-events: all;
  }
  &--sign {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }
}
.modal-form {
  width: 32.5%;
  background-color: $white;
  border-radius: 10px;
  padding: 30px;
  position: absolute;
  top: 181px;
  left: 50%;
  min-width: 624px;
  transform: translateX(-50%);
  transition: all .3s ease-in-out;
  z-index: -100;
  &__title {
    margin-bottom: 8px;
  }
  &__descr {
    margin-bottom: 30px;
    font-size: 14px;
    line-height: 19px;
    color: $black;
    width: 68%;
  }

}
.form-answer {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: $white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 10px;
  gap: 32px;
  transform: scaleY(0);
  opacity: 0;
  transition: all .3s ease-in-out;
  &--active {
    opacity: 1;
    transform: scaleY(1);
  }
}
@media (max-width: 1024px) {
  .modal-form {
    top: 184px;
  }
}
@media (max-width: 768px) {
  .modal-form {
    min-width: unset;
    width: calc(100% - 72px - 72px);
  }
}
@media (max-width: 768px) {
  .modal-form {
    width: calc(100% - 12px - 12px);
    padding: 16px;
    padding-top: 30px;
    &__descr {
      width: 100%;
      margin-bottom: 0;
    }
    .sign__input {
      margin-bottom: 15px;
    }
    .close {
      top: 16px;
      right: 16px;
      &__icon {
        width: 12px;
        height: 12px;
      }
    }
  }
  .form-answer {
    img {
      width: 73px;
      height: 50px;
    }
    .section-title {
      font-size: 16px;
      line-height: 100%;
    }
  }
}
