.close {
  outline: none;
  position: absolute;
  z-index: 500;
  top: 20px;
  right: 20px;
  cursor: pointer;
  font-size: 0;
}
.close__icon path {
  transition: fill 0.3s ease-in-out;
}
.close::after {
  content: "";
  position: absolute;
  right: -3px;
  left: -3px;
  top: -3px;
  bottom: -3px;
  background-color: #EBD1FF;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  z-index: -1;
}
.close:hover path {
  fill: #A65CF0;
}
.close:focus::after {
  opacity: 1;
}
.close:focus path, .close:active path {
  fill: #7033AC;
}

.stop-scroll {
  overflow: hidden;
}

.circle {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 2px solid #A65CF0;
  background-color: #fff;
  position: relative;
  transition: all 0.3s ease-in-out;
  outline: none;
}
.circle svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
