}
.circle svg path {
  transition: stroke 0.3s ease-in-out;
}
.circle:focus svg path, .circle:hover svg path, .circle:active svg path {
  stroke: #fff;
}
.circle:focus {
  background-color: #C998FB;
}
.circle:hover {
  background-color: #A65CF0;
}
.circle:active {
  background-color: #7033AC;
  border-color: #7033AC;
}
.circle--disabled {
  border-color: #999;
}
.circle--disabled svg path {
