.custom-checkbox {
  display: block;
}
.custom-checkbox__input {
  opacity: 0;
  position: absolute;
}
.custom-checkbox__text {
  display: inline-block;
  position: relative;
  padding-left: 22px;
}
.custom-checkbox__text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 1px solid #999;
  border-radius: 2px;
  transition: background-color 0.3s ease-in-out;
}
.custom-checkbox__text::after {
  content: "";
  position: absolute;
  left: 1px;
  top: 0;
  width: 20px;
  height: 20px;
  background-image: url(../img/check.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 15px 12px;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.custom-checkbox__input:checked + .custom-checkbox__text::before {
  background-color: #A65CF0;
  border-color: #A65CF0;
}
.custom-checkbox__input:checked + .custom-checkbox__text::after {
  opacity: 1;
}
.custom-checkbox__input:focus + .custom-checkbox__text::before {
  border-color: #A65CF0;
}
