html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  margin: 0;
  min-width: 320px;
  font-family:"Open Sans", sans-serif;
  font-weight: 400;
  scroll-behavior: smooth;
}
img {
  max-width: 100%;
  object-fit: cover;
}
a {
  text-decoration: none;
  color: inherit;
}
h1,h2,h3,h4,h5,p {
  margin: 0;
}
input {
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
}
.site-container {
  overflow: hidden;
}
button {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
  font-family: inherit;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type='number'] {
  -moz-appearance: textfield;
}
.container {
  margin: 0 auto;
  padding: 0 36px;
  max-width: 1352px;
}
.section-title {
  font-weight: 400;
  font-size: 32px;
  line-height: 100%;
  color: $black;
}
.card-title {
  font-weight: 400;
  font-size: 24px;
  line-height: 130%;
  color: $black;
}
.text-small {
  font-size: 14px;
  line-height: 100%;
  color: $grey;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
.price {
  font-size: 16px;
  line-height: 130%;
  color: $grey;
  &--line {
    text-decoration: line-through;
  }
}
.form-group {
  .custom-checkbox + .is-label-invalid {
    top: unset !important;
    bottom: -60%;
  }
}
.close {
  outline: none;
  position: absolute;
  z-index: 500;
  top: 20px;
  right: 20px;
  cursor: pointer;
  font-size: 0;
  &__icon {
    path {
      transition: fill .3s ease-in-out;
    }
  }
  &::after {
    content: "";
    position: absolute;
    right: -3px;
    left: -3px;
    top: -3px;
    bottom: -3px;
    background-color: $pink-violet;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    z-index: -1;
  }
  &:hover path{
    fill: $primary;
  }
  &:focus::after {
    opacity: 1;
  }
  &:focus path,
  &:active path{
    fill: $primary-shade;
  }
}
.stop-scroll {
  overflow: hidden;
}
.circle {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 2px solid $primary;
  background-color: $white;
  position: relative;
  transition: all .3s ease-in-out;
  outline: none;
  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    stroke: $primary;
  }
  svg path {
    transition: stroke .3s ease-in-out;
  }
  &:focus,
  &:hover,
  &:active {
    svg path {
      stroke: $white;
    }
  }
  &:focus {
    background-color: $primary-light;
  }
  &:hover {
    background-color: $primary;
  }
  &:active {
    background-color: $primary-shade;
    border-color: $primary-shade;
  }
  &--disabled {
    border-color: $grey;
    svg path {
      stroke: $grey;
    }
  }
  &--small {
    width: 22px;
    height: 22px;
  }
}
.nav-btns {
  position: absolute;
  right: 36px;
  top: 0;
  display: flex;
  gap: 13px;
}
.social {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  &__link {
    outline: none;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: -3px;
      right: -3px;
      left: -3px;
      bottom: -3px;
      background-color: $primary-light;
      z-index: 0;
      opacity: 0;
      transition: all .3s ease-in-out;
    }
    &:focus {
      .social__icon path{
        fill: $white;
      }
    }
    &:focus::after {
      opacity: 1;
    }
  }
  &__icon {
    width: 30px;
    height: 30px;
    display: block;
    position: relative;
    z-index: 1;
  }
}
.form-group {
  position: relative;
}
.swiper-slide {
  height: unset;
}
.link {
  transition: all .3s ease-in-out;
  position: relative;
  outline: none;
  z-index: 1;
  &::after {
    content: "";
    position: absolute;
    left: -10px;
    top: 0;
    right: -10px;
    bottom: 0;
    background-color: $pink-violet;
    z-index:-1;
    opacity: 0;
    transition: opacity .3s ease-in-out;
  }
  &:focus::after {
    opacity: 1;
  }
  &:hover {
    color: $primary;
  }
  &:active {
    color: $primary-shade;
  }
}
.logo {
  position: relative;
  outline: none;
  font-size: 0;
  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    outline: 2px solid $primary;
    outline-offset: 3px;
    opacity: 0;
    transition: opacity .3s ease-in-out;
  }
  &:focus::after {
    opacity: 1;
  }
}
.icon {
  position: relative;
  font-size: 0;
  outline: none;
  cursor: pointer;
  &::after {
    content: "";
    position: absolute;
    left: -4px;
    right: -4px;
    top: -4px;
    bottom: -4px;
    background-color: $primary-light;
    z-index: -1;
    opacity: 0;
    transition: opacity .3s ease-in-out;
  }
  path {
    transition: fill .3s ease-in-out;
  }
  &:focus {
    path {
      fill: $white;
    }
    &::after {
      opacity: 1;
    }
  }
  &:hover {
    path {
      fill: $primary-light;
    }
  }
  &:active {
    path {
      fill: $primary-shade;
    }
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 72px;
  }
  .nav-btns {
    right: 72px;
  }
  .social {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 576px) {
  .container {
    padding: 0 12px;
  }
  .nav-btns {
    right: 38%;
    top: unset;
    bottom: -47px;
  }
  .section-title {
    font-size: 24px;
    line-height: 100%;
  }
  .card-title {
    font-size: 16px;
    line-height: 22px;
  }
}
