.btn-fill {
  font-family:"Open Sans", sans-serif;
  padding: 22px 0;
  border-radius: 10px;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: $white;
  background-color: $primary;
  outline: none;
  text-align: center;
  transition: background-color .3s ease-in-out, outline .3s ease-in-out;
  &:focus {
    outline: 2px solid $primary-shade;
    background-color: $primary-light;
  }
  &:hover {
    background-color: $primary-light;
  }
  &:active {
    background-color: $primary-shade;
  }
  &--disabled {
    background-color: $md-grey;
    color: $grey;
    cursor: context-menu;
    &:focus {
      outline: none;
    }
    &:focus,
    &:hover,
    &:active {
      background-color: $md-grey;
    }
  }
}
.btn-stroke {
  font-family:"Open Sans", sans-serif;
  padding: 17px 33px;
  border-radius: 10px;
  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
  color: $primary;
  border: 2px solid $primary;
  outline: none;
  transition: all .3s ease-in-out;
  &:focus {
    background-color: $primary-light;
    color: $white;
  }
  &:hover {
    background-color: $primary;
    color: $white;
  }
  &:active {
    background-color: $primary-shade;
    border-color: $primary-shade;
  }
}
