.select {
  position: relative;
  &__top {
    outline: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      cursor: pointer;
    }
    &--active {
      + .select__bottom {
        transform: scaleY(1);
      }
      .select__icon {
        transform: rotate(180deg);
      }
    }
  }
  &__bottom {
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
    z-index: 20;
    background-color: $white;
    border: 2px solid $grey-light;
    transform-origin: top;
    transform: scaleY(0);
    transition: transform .3s ease-in-out;
  }
  &__title {
    color: $primary;
  }
  &__item {
    color: $black;
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
  &__icon {
    transition: transform .3s ease-in-out;
  }
}
.top-select {
  &__top {
    min-width: 84px;
    padding: 0 4px;
    path {
      transition: fill .3s ease-in-out;
    }
    &:focus {
      background-color: $pink-violet;
    }
    &:hover {
      path{
        fill: $primary-light;
      }
      .top-select__title {
        color: $primary-light;
      }
    }
    &:focus,
    &:active {
      path{
        fill: $primary-shade;
      }
      .top-select__title {
        color: $primary-shade;
      }
    }
  }
  &__title {
    transition: color .3s ease-in-out;
  }
  &__item {
    font-size: 14px;
    line-height: 130%;
    cursor: pointer;
  }
  &__list {
    padding: 16px;
  }
  .select__icon {
    margin-top: 6px;
  }
}

.search-select {
  background-color: $white;
  grid-column: span 3;
  padding: 17px 30px 17px 29px;
  z-index: 25;
  border: 2px solid $grey-light;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  &__title {
    font-size: 16px;
    line-height: 22px;
  }
  &__bottom {
    border-radius: 10px;
    padding: 22px 30px;
    display: flex;
    gap: 32px;
    width: 101%;
    flex-wrap: wrap;
  }
  &__item {
    font-size: 16px;
    line-height: 130%;
  }
  &__top{
    &::before {
      content: "";
      position: absolute;
      top: -1px;
      left: 0;
      bottom: 0;
      right: 0;
      width: 101%;
      border: 2px solid $primary;
      z-index: -1;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;
      opacity: 0;
      transition: all .3s ease-in-out;
    }
    &:focus::before {
      opacity: 1;
      background-color: $pink-light;
    }
    &:hover::before {
      opacity: 1;
    }
    &:active::before {
      border-color: $primary-shade;
    }
  }
  .select__icon {
    margin-top: 4px;
  }
}
