.footer {
  &__top {
    padding-top: 24px;
    padding-bottom: 22px;
    background-color: $md-grey;
  }
  &__content {
    @extend %grid;
  }
  &__left {
    grid-column: span 2;
  }
  &__right {
    grid-column-start: 5;
    grid-column-end: -1;
  }
  &__logo {
    margin-bottom: 31px;
    display: block;
  }
  &__navigation {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__nav-block {
    &:nth-child(2) {
      transform: translateX(-18px);
    }
    &:nth-child(3) {
      transform: translateX(-16px);
    }
    &:nth-child(4) {
      transform: translateX(-2px);
    }
    &:not(:nth-child(4n)) {
      margin-right: 78px;
    }
  }
  &__list-title {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: $black;
    margin-bottom: 0;
  }
  &__item {
    &:not(:last-child) {
      margin-bottom: 2px;
    }
  }
  &__bottom {
    padding: 6px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $grey-light;
  }
  &__copy {
    font-size: 14px;
    line-height: 19px;
    color: $grey;
  }
  &__nav-link {
    &:focus {
      color: $primary-shade;
    }
  }
}
@media (max-width: 1024px) {
  .footer {
    &__right {
      grid-column-start: 3;
    }
  }
}
@media (max-width: 768px) {
  .footer {
    &__content {
      grid-row-gap: 24px;
    }
    &__navigation {
      flex-wrap: wrap;
      gap: 32px;
    }
    &__nav-block {
      &:not(:nth-child(4n)) {
        margin-right: 12px;
      }
    }
    &__left {
      grid-column: span 6;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__right {
      grid-column: span 6;
    }
    &__logo {
      margin-bottom: 0;
    }
  }
}
@media (max-width: 576px) {
  .footer {
    &__left {
      flex-direction: column;
      gap: 24px;
    }
    &__navigation {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 19px;
      grid-row-gap: 24px;
    }
    &__nav-block {
      &:nth-child(2),
      &:nth-child(3) {
        transform: none;
      }
      &:not(:nth-child(4n)) {
        margin-right: 0;
      }
    }
    &__list-title {
      line-height: 126%;
    }
  }
}
