.cooperation {
  padding-top: 29px;
  padding-bottom: 70px;
  &__content {
    @extend %grid;
  }
  &__title {
    margin-bottom: 34px;
  }
  &__block {
    margin-bottom: 1px;
    grid-column: span 8;
    &:nth-child(2) {
      grid-column: span 6;
      margin-bottom: 2px;
    }
    &:nth-child(3) {
      .cooperation__text {
        line-height: 181%;
      }
    }
  }
  &__title-section {
    margin-bottom: 13px;
  }
  &__text {
    font-size: 16px;
    line-height: 180%;
    color: $grey-shade;
    &:not(:last-child) {
      margin-bottom: 12px;
    }
    &--image {
      position: relative;
      padding-left: 37px;
      line-height: 150%;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 25px;
        height: 25px;
      }
    }
    &--network::after {
      background-image: url(../img/network.svg);
    }
    &--briefcase::after {
      background-image: url(../img/briefcase.svg);
    }
    &--request::after {
      background-image: url(../img/request.svg);
    }
    &--garanty {
      padding-left: 26px;
      position: relative;
      &::after {
        position: absolute;
        top: 0;
        left: 0;
        width: 14px;
        font-weight: 600;
        font-size: 16px;
        line-height: 180%;
        color: $secondary;
      }
      &:nth-child(2)::after {
        content: "1.";
      }
      &:nth-child(3)::after {
        content: "2.";
      }
      &:nth-child(4)::after {
        content: "3.";
      }
    }
  }
  &__opportunity {
    font-size: 16px;
    line-height: 22px;
    color: $secondary;
    margin-bottom: 12px;
  }
  &__garanty {
    min-height: 116px;
    grid-column: span 6;
    display: flex;
    align-items: center;
    padding-left: 109px;
    background-image: url(../img/garanty.webp);
    background-repeat: no-repeat;
    background-position: top left;
    background-size: 77px 116px;
    .card-title {
      line-height: 150%;
    }
  }
}
@media (max-width: 768px) {
  .cooperation {
    padding-bottom: 69px;
    &__text--garanty {
      line-height: 181%;
    }
    &__block {
      grid-column: span 6;
    }
  }
}
@media (max-width: 576px) {
  .cooperation {
    padding-top: 0;
    padding-bottom: 70px;
    &__title {
      margin-bottom: 32px;
    }
    &__text {
      font-size: 14px;
      line-height: 178%;
      &--image {
        line-height: 150%;
      }
      &--garanty {
        line-height: 179%;
      }
    }
    &__block {
      margin-bottom: 16px;
      &:nth-child(2) {
        margin-bottom: 16px;
      }
      &:nth-child(3) {
        .cooperation__title-section {
          font-size: 14px;
          line-height: 19px;
        }
        .cooperation__text {
          line-height: 178%;
        }
      }
      &:nth-child(4) {
        margin-bottom: 14px;
      }
    }
    &__opportunity:first-child {
      margin-bottom: 11px;
    }
    &__garanty {
      grid-column: span 5;
      min-height: 72px;
      padding-left: 52px;
      background-size: 36px 54px;
      background-position: left center;
    }
  }
}
