.articles {
  padding-top: 35px;
  padding-bottom: 35px;
  &__container {
    position: relative;
  }
  &__title {
    margin-bottom: 26px;
  }
}
.article {
  border-radius: 10px;
  background-color: $grey-light;
  .card-title {
    line-height: 137%;
  }
  &__content {
    padding: 30px;
    padding-top: 32px;
    min-height: 251px;
    height: calc(100% - 426px);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
  }
  &__title {
    margin-bottom: 20px;
  }
  &__image {
    width: 100%;
    height: 426px;
    display: block;
  }
}
@media (max-width: 1024px) {
  .articles {
    padding-bottom: 38px;
  }
  .article {
    grid-column: span 3;
    &__content {
      height: calc(100% - 202px);
      padding-top: 30px;
      padding-bottom: 27px;
    }
    &__image {
      height: 202px;
    }
    .card-title {
      font-size: 16px;
      line-height: 22px;
      width: 97%;
    }
  }
}
@media (max-width: 576px) {
  .articles {
    padding-top: 40px;
    padding-bottom: 58px;
  }
  .article {
    min-height: 456px;
  }
}
