.similar {
  padding-top: 40px;
  padding-bottom: 70px;
  &__container {
    position: relative;
  }
  &__title {
    font-size: 24px;
    line-height: 130%;
    margin-bottom: 24px;
  }
  .similar-swiper {
    .swiper-slide {
      padding: 0;
    }
  }
}
.btn.swiper-button-disabled {
  &:hover {
    border-color: $grey;
    background-color: transparent;
    svg path {
      stroke: $grey;
    }
  }
  border-color: $grey;
  cursor: context-menu;
  svg path {
    stroke: $grey;
  }
}
@media (max-width: 1024px) {
  .card--hide {
    display: none;
  }
}
@media (max-width: 576px) {
  .similar {
    padding-bottom: 118px;
    &__title {
      margin-bottom: 23px;
    }
    .card__image {
      width: 77%;
      bottom: 66%;
    }
    .btn-stroke {
      padding: 10px 23px;
    }
    .card-title {
      line-height: 127%;
    }
  }
}
