.hero {
  &__container {
    @extend %grid;
  }
  &__content {
    grid-column: span 5;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-column-gap: 32px;
    grid-row-gap: 46px;
    padding: 80px 0;
  }
  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 67px;
    color: $grey;
    grid-column: span 5;
  }
  &__btn {
    grid-column: span 2;
  }
  &__btn320 {
    display: none;
  }
}
.swiper-slide {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  &--1 {
    background-image: url('../img/hero1-1920.webp');
  }
  &--2 {
    background-image: url('../img/hero2-1920.webp');
  }
  &--3 {
    background-image: url('../img/hero3-1920.webp');
    .hero__content {
      grid-column: span 6;
    }
    .hero__title {
      color: $white;
    }
  }
}
.swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin-left: 0;
  margin-right: 0;
}
.swiper-pagination-bullet {
  width: 14px;
  height: 14px;
  background: rgba($black, .3);
  opacity: 1;
  &:not(:last-child) {
    margin-right: 16px !important;
  }
}
.swiper-pagination-bullet-active {
  background: $black;
}
.swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 18px;
}

@media (max-width: 1024px) {
  .swiper-slide {
    &--1 {
      background-image: url('../img/hero1-1024.webp');
    }
    &--2 {
      background-image: url('../img/hero2-1024.webp');
    }
    &--3 {
      background-image: url('../img/hero3-1024.webp');
    }
  }
}
@media (max-width: 768px) {
  .swiper-slide {
    &--1 {
      background-image: url('../img/hero1-768.webp');
    }
    &--2 {
      background-image: url('../img/hero2-768.webp');
    }
    &--3 {
      background-image: url('../img/hero3-768.webp');
    }
  }
}
@media (max-width: 576px) {
  .swiper-slide {
    &--1 {
      background-image: url('../img/hero1-320.webp');
    }
    &--2 {
      background-image: url('../img/hero2-320.webp');
    }
    &--3 {
      background-image: url('../img/hero3-320.webp');
    }
  }
  .hero {
    &__title {
      font-size: 20px;
      line-height: 28px;
    }
    &__btn {
      display: none;
    }
    &__btn320 {
      display: block;
      width: calc(100% - 24px);
      margin: 0 12px;
    }
    &__content {
      padding-top: 30px;
      min-height: 210px;
      padding-left: 18px;
    }
  }
  .hero-swiper {
    margin-bottom: 32px;
  }
  .swiper-pagination-clickable .swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    margin-right: 8px !important;
  }
  .swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: 5px;
    left: 4px;
  }
}
