.burger {
  display: none;
}
.header {
  &__main {
    padding-top: 34px;
    padding-bottom: 31px;
    @extend %grid;
    align-items: center;
    .icons {
      display: none;
    }
  }
  &__logo {
    grid-column: span 2;
  }
  &__nav {
    grid-column-start: 4;
    grid-column-end: -1;
    .nav__link {
      font-size: 16px;
      line-height: 22px;
      color: $black;
    }
    .nav__list {
      margin-right: 3px;
    }
    .nav__item:not(:nth-child(6n)) {
      margin-right: 91px;
    }
  }
  &__bottom {
    @extend %grid;
    padding-bottom: 32px;
    align-items: center;
  }
  &__add {
    display: none;
  }
  &__nav-close {
    display: none;
  }
}
.header-top {
  background-color: $grey-light;
  padding-top: 15px;
  padding-bottom: 13px;
  &__left {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    margin-right: 100px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
  }
  .select {
    margin-right: 28px;
    display: inline-block;
  }
  .phone {
    display: inline-block;
  }
  .nav__link {
    position: relative;
    &:focus {
      color: $primary-shade;
    }
  }
  .nav__item {
    &:not(:nth-child(4n)) {
      margin-right: 65px;
    }
  }
}
.phone-link {
  display: flex;
  align-items: center;
  color: $secondary;
  transition: color .3s ease-in-out;
  outline: none;
  path {
    transition: fill .3s ease-in-out;
  }
  &:focus {
    background-color: $bone
  }
  &:hover {
    color: $secondary-light;
    path {
      fill: $secondary-light;
    }
  }
  &:active {
    color: $secondary-shade;
    path {
      fill: $secondary-shade;
    }
  }
  svg {
    margin-right: 2px;
  }
}
.nav {
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
}
.search {
  grid-column: span 10;
  display: grid;
  gap: 27px;
  grid-template-columns: repeat(10, 1fr);
  border-radius: 10px;
  position: relative;
  &__icon {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    z-index: 3;
  }
  &__input {
    outline: none;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    grid-column: span 7;
    padding: 18px 80px 16px 34px;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: $black;
    outline: none;
    position: relative;
    z-index: 2;
    font-family: inherit;
    background-color: $grey-light;
    margin-right: -26px;
    display: flex;
    align-items: center;
    &::placeholder {
      color: $grey;
      font-size: 16px;
      font-family: 'Open Sans', sans-serif;
      line-height: 120%;
      background-image: url('../img/search.svg');
      background-repeat: no-repeat;
      background-size: 16px 16px;
      padding-left: 25px;
      background-position: left 3px;
    }
    &:focus {
      outline: 2px solid $primary;
      background-color: $pink-light;
    }
    &:hover {
      background-color: $md-grey;
    }
  }
  &__circle {
    position: absolute;
    right: 31.3%;
    top: 50%;
    z-index: 3;
    background-color: transparent;
    transform: translateY(-50%);
  }
}
.icons {
  display: flex;
  gap: 32px;
  grid-column: span 2;
  justify-content: end;
  &__cart {
    position: relative;
    padding-left: 11px;
  }
  &__cart-count {
    position: absolute;
    left: 0;
    top: 5px;
    border-radius: 50%;
    width: 14px;
    height: 15px;
    padding: 3px 4px;
    font-weight: 700;
    font-size: 10px;
    line-height: 100%;
    color: $white;
    background-color: $secondary;
  }
}
@media (max-width: 1350px) {
  .header__nav .nav__item:not(:nth-child(6n)) {
    margin-right: 66px;
  }
}
@media (max-width: 1240px) {
  .header-top .nav__item:not(:last-child) {
    margin-right: 25px;
  }
  .header__nav .nav__item:not(:nth-child(6n)) {
    margin-right:40px;
  }
  .header-top {
    padding: 18px 0;
    background-color: $white;
    &__container {
      position: relative;
    }
    .header-top-nav {
      display: none;
    }
  }
  .phone-link {
    position: absolute;
    right: 36px;
  }
  .header {
    &__main {
      padding-top: 32px;
      padding-bottom: 34px;
    }
    &__nav {
      grid-column-start: 3;
      .nav__list {
        margin-left: 32px;
        margin-right: 2px;
      }
    }
    &__bottom {
      .icons {
        display: none;
      }
    }
    &__add {
      display: block;
      background-color: $grey-light;
      padding-top: 10px;
      padding-bottom: 12px;
      .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      .nav__item {
        &:not(:last-child) {
          margin-right: 64px;
        }
      }
    }
  }

    .search {
      grid-column: span 9;
      grid-template-columns: repeat(9, 1fr);
      &__circle {
        top: 50%;
        right: 35%;
      }
      &__input {
        grid-column: span 6;
      }
    }
}
@media (max-width: 1023px) {
  .phone-link {
    right: 72px;
  }
  .search-select {
    grid-column: span 2;
  }
  .search {
    grid-template-columns: repeat(6, 1fr);
  }
  .search__input {
    grid-column: span 4;
  }
  .search__circle {
    right:35.4%;
  }
  .header {
    &__main {
      grid-template-columns: repeat(6, 1fr);
    }
    &__logo {
      grid-column-start: 3;
      grid-column-end: 5;
    }
    &__main {
      padding-bottom: 32px;
      .icons {
        display: flex;
      }
    }
    &__main-container {
      position: relative;
    }
    &__nav {
      position: absolute;
      top: 0;
      left: 0;
      min-height: 300px;
      background-color: $white;
      background-image: url('../img/back-nav.svg');
      background-repeat: no-repeat;
      background-position: bottom 60px right 72px;
      background-size: 161px 112px;
      width: 100%;
      padding-top: 34px;
      padding-left: 72px;
      z-index: 300;
      transform: scaleY(0);
      transform-origin: top;
      transition: all .3s ease-in-out;
      &--active {
        transform: scaleY(1);
      }
      .nav__list {
        margin: 0;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: start;
        gap: 41px;
        max-height: 148px;
        max-width: 288px;
      }
    }
    &__nav-close {
      display: block;
      position: unset;
      margin-bottom: 34px;
    }
    &__add {
      padding-top: 13px;
      padding-bottom: 16px;
      .icons {
        display: none;
      }
      .header-top-nav {
        width: 100%;
      }
      .nav__item {
        &:not(:last-child) {
          margin-right: 0;
        }
      }
    }
  }
  .burger {
    display: block;
    position: relative;
    outline: none;
    width: 24px;
    height: 24px;
    &__line {
      position: absolute;
      left: 0;
      background-color: $primary;
      height: 3px;
      border-radius: 4px;
      width: 100%;
      &:first-child {
        top: 0;
      }
      &:nth-child(2) {
        top: 50%;
        transform: translateY(-50%);
      }
      &:last-child {
        bottom: 0;
      }
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      outline: 2px solid $primary;
      outline-offset: 4px;
      opacity: 0;
      transition: opacity .3s ease-in-out;
    }
    &:focus::after {
      opacity: 1;
    }
  }
}
@media (max-width: 900px) {
  .header__add .nav__item:not(:last-child) {
    margin-right: 25px;
  }
  .header__nav .nav__list {
    margin-left: 0;
  }
}

@media (max-width: 766px) {
  .header {
    &__add {
      display: none;
    }
    &__main-container {
      position: unset;
    }
    &__nav {
      padding: 12px;
      background-image: none;
      top: 130px;
      height: 100%;
      .nav__list {
        flex-wrap: nowrap;
      }
    }
    &__main {
      padding: 10px 0;
      .header__logo {
        padding-top: 12px;
        padding-bottom: 11px;
        position: absolute;
        min-width: 320px;
        top: 0;
        left: 0;
        background-color: $grey-light;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        &::after {
          width: unset;
          left: 60px;
          right: 58px;
          top: 8px;
          bottom: 8px;
        }
      }
      .icons {
        grid-column-start: 5;
        grid-column-end: -1;
      }
    }
    &__bottom {
      position: relative;
      padding-top: 17px;
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: -12px;
        height: 1px;
        width: 110%;
        background-color: $grey-light;
      }
    }
  }
  .burger {
    margin-left: 4px;
  }
  .phone-link {
    position: unset;
    margin-left: 2px;
  }
  .header-top {
    padding-top: 67px;
    border-bottom: 1px solid $grey-light;
    &__left {
      flex-wrap: wrap;
      align-items: start;
    }
    .select {
      margin-right: 30%;
    }
  }
  .top-select {
    margin-bottom: 18px;
  }
  .search {
    grid-column: span 6;
    gap: 16px;
    &__input {
      grid-column: span 6;
      border-radius: 10px;
      padding: 22px 110px 19px 34px;
      margin-right: 0;
    }
    &__circle {
      right: 11%;
      top: 22%;
    }
  }
  .search-select {
    grid-column: span 6;
    border-radius: 10px;
    margin-left: 0;
  }

}
