.offers {
  padding-top: 71px;
  padding-bottom: 35px;
  &__container {
    position: relative;
  }
  &__title {
    margin-bottom: 26px;
    line-height: 99%;
  }
  &__content {
    min-height: 511px;
    .swiper-slide {
      padding: 0;
    }
  }
  &__decor {
    position: absolute;
    left: 0;
    top: 30px;
    z-index: 20;
    width: 92px;
    height: 33px;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: $white;
    background-image: url(../img/decor.webp);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    span {
      width: calc(100% - 10px);
      top: 50%;
      transform: translateY(-50%);
      text-align: center;
      position: absolute;
    }
  }
  &__card {
    min-height: inherit;
    gap: 22px;
    padding: 30px;
    width: 296px;
    .card-title {
      line-height: 128%;
    }
    .card__top {
      height: 282px;
    }
    .card__bottom {
      height: calc(100% - 282px);
    }
  }
  &__card--big {
    grid-column: span 6;
    width: 624px;
  }
  &__price {
    font-size: 18px;
    line-height: 127%;
    color: $secondary;
    &:first-child {
      margin-right: 26px;
    }
  }
  &__price-block {
    margin-bottom: 21px;
  }
  &__image-bottom {
    position: absolute;
    bottom: 30px;
    right: 30px;
  }
}
@media (max-width: 1352px) {
  .offers__image-bottom {
    display: none;
  }
}
@media (max-width: 1024px) {
  .offers {
    &__card--big {
      grid-column: span 3;
    }
  }
}
@media (max-width: 768px) {
  .offers__title {
    margin-right: 75px;
  }
}
@media (max-width: 576px) {
  .offers {
    padding-bottom: 59px;
    &__title {
      margin-right: 0;
    }
    &__card {
      .card-title {
        font-size: 24px;
        line-height: 130%;
        margin-bottom: 20px;
      }
      .card__bottom {
        padding: 30px;
        padding-top: 16px;
      }
      .btn-stroke {
        padding: 17px 33px;
      }
    }
    &__price-block {
      margin-bottom: 23px;
    }
  }
}
