.catalog {
  padding-top: 30px;
  padding-bottom: 70px;
  .swiper-slide {
    padding: 0;
  }
  &__container {
    @extend %grid;
  }
  &__block {
    grid-column: span 9;
  }
  &__title {
    margin-bottom: 24px;
    font-size: 24px;
    line-height: 130%;
  }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.aside {
  grid-column: span 3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__title {
    font-weight: 400;
    font-size: 18px;
    line-height: 115%;
    color: $black;
    margin-bottom: 22px;
  }
  &__bottom {
    min-height: 360px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  &__bottom-content {
    background-color: $black;
    height: 100%;
    padding: 30px;
  }
  &__bottom-title {
    font-family: 'Playfair Display';
    font-weight: 900;
    font-size: 24px;
    line-height: 30px;
    color: $gold;
  }
  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: $grey;
    width: 90%;
    margin-bottom: 13px;
  }
  &__catalog-link {
    display: flex;
    align-items: center;
    gap: 5px;
  }
  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: $primary;
  }
  .circle {
    background-color: transparent;
  }
}
.filter {
  display: flex;
  flex-direction: column;
  gap: 27px;
  &__title {
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
    color: $grey-shade;
    margin-bottom: 9px;
  }
  &__item {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
  &__checkbox-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 132%;
    color: $grey-shade;
    padding-left: 32px;
  }
  &--top {
    display: none;
  }
  .select__bottom {
    min-width: 100%;
    width: fit-content;
  }
}
.filter-price {
  margin-bottom: 10px;
  &__text,
  &__input {
    font-size: 16px;
    line-height: 130%;
    color: $grey-shade;
  }
  &__title {
    margin-bottom: 4px;
  }
  &__inputs {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 9px;
  }
  &__text {
    margin-right: 6px;
  }
  &__input {
    outline: none;
    font-family: inherit;
    border: 1px solid $grey;
    border-radius: 2px;
    padding: 5px 12px 3px;
    width: 86px;
    transition: all .3s ease-in-out;
    &:focus {
      background-color: $pink-violet;
    }
    &:hover,
    &:focus {
      border-color: $primary;
    }
    &:active {
      border-color: $primary-shade;
    }
  }
  .noUi-connect {
    background-color: $primary;
  }
  .noUi-target {
    border-radius: 3px;
    background-color: $md-grey;
    border: none;
    box-shadow: none;
  }
  .noUi-horizontal {
    height: 3px;
  }
  .noUi-handle:after, .noUi-handle:before {
    display: none;
  }
  .noUi-handle {
    border: none;
    box-shadow: none;
    cursor: pointer;
  }
  .noUi-horizontal .noUi-handle {
    width: 13px;
    height: 13px;
    background-color: transparent;
    top: 0;
    right: -7px;
    background-image: url(../img/bg.svg);

  }
  .noUi-handle.noUi-handle-upper {
    right: -5px;
  }
}
.price-label {
  &__text,
  &__input {
    font-size: 16px;
    line-height: 130%;
    color: $grey-shade;
  }
  &__text {
    margin-right: 6px;
  }
  &__input {
    outline: none;
    font-family: inherit;
    border: 1px solid $grey;
    border-radius: 2px;
    padding: 5px 12px 3px;
    width: 86px;
    transition: all .3s ease-in-out;
    &:focus {
      background-color: $pink-violet;
    }
    &:hover,
    &:focus {
      border-color: $primary;
    }
    &:active {
      border-color: $primary-shade;
    }
  }
}
.navigation {
  display: flex;
  grid-column-start: 8;
  grid-column-end: 9;
  .swiper-pagination-bullet {
    width: 32px;
    height: 32px;
    font-weight: 600;
    font-size: 16px;
    line-height: 100%;
    color: $primary;
    border-radius: 10px;
    border: 2px solid $primary;
    transition: all .3s ease-in-out;
    margin: 0;
    background-color: $white;
    text-align: center;
    padding-top: 6px;
    transition: all .3s ease-in-out;
    outline: none;
    &:focus {
      background-color: $primary-light;
      color: $white;
    }
    &:hover {
      background-color: $primary;
      color: $white;
    }
  }
  .swiper-pagination-bullet.swiper-pagination-bullet-active {
    border-color: $primary-shade;
    background-color: $primary-shade;
    color: $white;
    cursor: context-menu;
  }
}
.swiper-slide {
  padding: 0;
}
@media (max-width: 1024px) {
  .aside {
    display: none;
  }
  .navigation {
    grid-column-start: 5;
    grid-column-end: 6;
  }
  .catalog {
    &__title {
      margin-bottom: 15px;
    }
  }
  .filter {
    &--top {
      display: grid;
      grid-template-columns: repeat(9, 1fr);
      gap: 32px;
      align-items: center;
      margin-bottom: 32px;
    }
    &__title {
      font-size: 14px;
      color: $black;
      margin-bottom: 0;
      font-weight: 400;
    }
    &__select {
      grid-column: span 2;
      background-color: $grey-light;
      padding: 9px 15px;
      border-radius: 10px;
    }
    &__select-top {
      position: relative;
      z-index: 1;
      &::before {
        content: "";
        position: absolute;
        top: -41%;
        left: -11%;
        bottom: -42%;
        right: -11%;
        border: 2px solid $primary;
        border-radius: 10px;
        opacity: 0;
        z-index: -1;
        transition: all .3s ease-in-out;
      }
      &:focus::before {
        opacity: 1;
        background-color: $pink-light;
      }
      &:hover::before {
        opacity: 1;
      }
      &:active::before {
        border-color: $primary-shade;
      }
    }
    &__label {
      margin-bottom: 12px;
      display: block;
    }
    &__item-add {
      font-size: 16px;
      line-height: 130%;
      color: $primary;
    }
    .select__title {
      color: $black;
    }
    .select__bottom {
      padding:22px 13px 30px 30px;
      z-index: 20;
      border-radius: 10px;
      margin-top: 2px;
    }
  }
}
@media (max-width: 768px) {
  .catalog {
    &__title {
      margin-bottom: 26px;
    }
  }
  .filter {
    &--top {
      grid-template-columns: repeat(4, 1fr);
      grid-row-gap: 16px;
      width: 65%;
    }
    &__title {
      grid-column: span 4;
    }
  }
  .navigation {
    margin-left: -32px;
    grid-column-start: 4;
  }
}
@media (max-width: 576px) {
  .breadcrumbs {
    display: none;
  }
  .catalog {
    padding-top: 0;
    &__title {
      margin-bottom: 16px;
    }
    .card__image {
      bottom: 0;
      max-width: 78%;
      left: 50%;
    }
    .card-title {
      line-height: 128%;
    }
    .btn-stroke {
      padding: 10px 23px;
    }
  }
  .catalog-card {
      min-height: 278px;
      gap: 16px;
  }
  .filter {
    &--top {
      width: 100%;
      gap: 16px;
    }
  }
  .navigation {
    gap: 8px;
    grid-column-start: 3;
    grid-column-end: -1;
    margin-left: -4px;
    margin-top: 16px;
  }
}
